.header-box {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  -moz-column-gap: 20px;
       column-gap: 20px;
  grid-template-areas: "k k k k k mn mn mn mn mn mn mn";
  z-index: 2;
  position: fixed;
  top: 12;
  width: calc(100% - 24px);
}
.header-box.layout {
  padding: 12px 12px 0 12px;
}
.header-box .name {
  grid-area: k;
  font-size: 1.8rem;
}
.header-box .main-menu.items {
  grid-area: mn;
  display: flex;
  justify-content: space-between;
}
.header-box .main-menu.items:hover {
  cursor: ne-resize;
}
.header-box .main-menu.items a {
  width: -moz-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.header-box .main-menu.items a .item {
  font-size: 1.8rem;
  display: flex;
}
.header-box .main-menu.items a .item span {
  font-family: monospace;
  color: #F1C9FF;
  width: 20px;
  display: block;
  text-align: center;
  align-self: center;
  padding-top: 2px;
}
.header-box .main-menu.items a .item:hover {
  transition: all ease 0.2s 0s;
  background-color: #0047ff;
  color: #0047ff;
  cursor: e-resize;
}
.header-box .main-menu.items a .item:hover > span {
  transition: all ease 0.2s 0s;
  color: #0047ff;
}

.header-active .name {
  color: #F1C9FF;
  background-color: #000000;
  width: -moz-fit-content;
  width: fit-content;
  transition: all ease 1s 0s;
}
.header-active .main-menu .item {
  color: #F1C9FF;
  background-color: #000000;
  width: -moz-fit-content;
  width: fit-content;
  transition: all ease 1s 0s;
}

.header-notactive .name {
  color: #000000;
  background-color: none;
  width: -moz-fit-content;
  width: fit-content;
  transition: all ease 0.5s 0s;
}
.header-notactive .main-menu .item {
  color: #000000;
  background-color: none;
  width: -moz-fit-content;
  width: fit-content;
  transition: all ease 0.5s 0s;
}

@media screen and (max-width: 900px) {
  .header-box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 64px;
    width: calc(100% - 40px);
  }
  .header-box.layout {
    padding: 0;
  }
  .header-box .name {
